@import url(https://fonts.googleapis.com/css2?family=Itim&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
header {
  background-color: #282c34;
  min-height: 4vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: white;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

header h1 {
  /* font-family: 'Acme', sans-serif; */
    color: white;
    margin: 16px 16px 16px 20px;
    cursor: pointer;
    font-family: 'Itim', cursive;
    font-weight: 200;

}
footer{

  font-family: 'Itim', cursive;
  text-align: center;
  width: 100%;
  height: 2.4rem;
  position: absolute;
  bottom: 0;
}
.note{
  min-width: 200px;
  max-width:260px;
  margin: 20px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius: 8px;
  float: left;
  font-family: 'Itim', cursive;
  /* to keep it in a row */

}
.note h3 {
  padding: 10px 10px 2px 13px;
  font-family: 'Itim', cursive;
  font-size: 25px;

}
.note p{
  padding: 1px 10px 8px 13px;
  font-size: 23px;
}
.note button{
  /* background-color: #282c34; */
    color: white;
    font-family: 'Itim', cursive;
    padding: 3px;
    border: none;
    border-radius: 50%;
    float: right;
    margin: 10px;
    background-color: #666666;
    transition: box-shadow 0.25s ease, background-color 0.25s ease, -webkit-transform 0.25s ease;
    transition: transform 0.25s ease, box-shadow 0.25s ease, background-color 0.25s ease;
    transition: transform 0.25s ease, box-shadow 0.25s ease, background-color 0.25s ease, -webkit-transform 0.25s ease;
    box-shadow: 0 4px 6px rgba(50,50,93,.11), 0 1px 3px rgba(0,0,0,.08);


}

.note button:hover {
    -webkit-transform: translate3d(0px,-1px,0px);
            transform: translate3d(0px,-1px,0px);
    background-color: #404040;
    box-shadow: 0 7px 14px rgba(50,50,93,.1), 0 3px 6px rgba(0,0,0,.08);
}

.addNote form{
  font-family: 'Itim', cursive;
  width: 500px;
  border-radius: 7px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  position: relative;
  margin: 30px auto 40px auto;
  background: #fff;
  padding: 15px;
}
.addNote form input,
.addNote form textarea {

  width: 100%;
  border: none;
  padding: 4px;
  outline: none;
  font-size: 1.2em;
  font-family: inherit;
  resize: none;
}
::-webkit-input-placeholder {
    font-family: 'Itim', cursive;
}
:-ms-input-placeholder {
    font-family: 'Itim', cursive;
}
::placeholder {
    font-family: 'Itim', cursive;
}
.btn-form{
  background-color: #666666;
  border: none;
  border-radius: 50%;
  color: white;
  height: 20px;
  width: 20px;
  cursor: pointer;
  /* box-shadow: 0 4px 6px rgba(50,50,93,.11), 0 1px 3px rgba(0,0,0,.08); */
  float: right;
  position: absolute;
  right: 18px;
  bottom: 11px;
}

